.diaryscreen {
    width: 100vw;
    height: 100vh;
    background-color: var(--page-background-color);

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: top;
        justify-content: top;
    -ms-flex-align: center;
        align-items: center;
}

.date {
    margin: 20px 50px;
    border-bottom: 2px solid black;
    font-family: var(--large-heading-font-family);
    font-size: var(--large-heading-font-size);
}

.entry {
    width: 90%;
    height: 80%;
    font-family: var(--note-entry-font-family);
    font-size: var(--note-entry-font-size);
    overflow: auto;
    text-align: left
}

.footer {
    width: 100%;
    position: absolute;
    bottom: 0;
    background-color: var(--footer-background-color);

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.homebutton {
    font-family: var(--small-text-font-family);
    font-size: var(--small-text-font-size);
}

.leftbutton {
    width: 34px;
    height: 34px;
    background-image: url(./back.png);
    background-size: cover;
    font-family: var(--small-text-font-family);
    font-size: var(--small-text-font-size);
}

.rightbutton {
    width: 34px;
    height: 34px;
    background-image: url(./forward.png);
    background-size: cover;
    font-family: var(--small-text-font-family);
    font-size: var(--small-text-font-size);
}



